//Variables
$sse-font: 'Segoe UI';
$sse-color: #333333;
$component-page-header-size:38px;
$cseo-brand-primary: #0078D7;

p {
    line-height: normal;
}
 a {
     color: $cseo-brand-primary;
 }
 a:hover {
     color:#0079A5;
 }
 pre {
    white-space: pre-wrap;
    word-wrap: break-word;
    text-align: justify;
 }
.font-medium {
    font-size: 14px;
}
.GlobalHeader {
    font-family: $sse-font;
    font-size: 18px;
    color:$sse-color;
    padding: 40px 200px 0 40px;
}
.HomeHeader {
    font-family: $sse-font;
    font-size: 18px;
    color:$sse-color;
    padding: 40px 200px 0 90px;
}
.global-links{
    color: $cseo-brand-primary;
}
.SubHeader {
    font-size: 20px;
    font-weight: 600;
    padding: 0px 0px 0px 0px;
}
.header-small {
    font-size: 18px;
    font-weight: 600;
}
.homepage-borders {
    color: $cseo-brand-primary;
    width: 30px;
    height: 0px;
}
.semibold{
    font-weight: 600;
}
.PageHeader {
    flex-grow: 1;
    flex-wrap: wrap;
    margin: 0 200px 0 0px;
    color: $sse-color;
    font-size: $component-page-header-size;
    font-family: 'Segoe UI';
    text-align: left; 
    font-weight: 700;
}
.PageContent {
    background: #FAFAFA;
    min-height: 100vh;
    overflow: hidden;
}
.HomePageContent {
    background: #0078D7;
    padding-bottom: 90px;
    color: #fff;
}
.HomePageContent-section-2 {
    background: #f2f2f2;
    padding-bottom: 90px;
    .text-header {
        color: $sse-color;
        font-weight: 600;
        padding: 5px 0px 20px 0;
    }
    .text-body {
        color: $sse-color;
    }
}

.Homepage-section-2-content{
    font-size: 16px;
    display: flex;
    margin: 60px 300px 0px 90px;
    line-height: normal;
    .left-section{
        padding: 0 50px 0 0;
    }
}
.HomePageContent-section-3 {
    background: #fafafa;
    padding-bottom: 90px;
    .text-header {
        font-size: 18px;
        font-weight: bold;
        color: $sse-color;
    }
}
.PageContent-spacing{
    padding: 30px 0px 20px 40px;
    
}
.Homepage-header-text {
    font-size: 44px;
    font-weight: 100;
    display: flex;
    margin: 100px 300px 0px 90px;
    line-height: normal;
}
.link-devs {
    padding: 0px 0px 0px 0;
}
.Homepage-contact-link {
    color: white;
    font-size: 16px;
    display: flex;
    float: right;
    padding: 0px 20px 0px 0;
}
.Homepage-contact-link:hover {
    color: #c3c3c3;
}
.Homepage-desc {
    font-size: 16px;
    font-weight: 500;
    display: inline-flex;
    width: 50%;
    padding: 10px 50px 0 60px;
    line-height: normal;
}
.Home-cseo-header {
    display: inline;
    color: white;
    font-weight: bold;
}
.Page-cseo-header {
    display: inline;
    color: #333333;
    font-weight: bold;
}
.Intro {
    margin: 20px 200px 20px 0px;
    line-height: normal;
}
.Content {
    margin: 20px 200px 20px 0px;
    line-height: normal;
}
.CardImage-spacing{
    padding: 30px 0px 60px 0px;
    
}
.CodeIntro {
    margin: 30px 200px 50px 0px;
    line-height: normal;
}
.Code-list-col1 {
    width: 20%;
    padding: 20px 0 0 0;
}
.Code-list-spacing {
    padding: 20px 0 0 100px;
}
.CodeBody {
    margin: 30px 0px 50px 0px;
    line-height: normal;
}
.Code-inline {
    background: white;
}
.code-snippet {
    background: #efefef;
    border: 1px solid #a9a9a9;
    padding: 20px 20px 20px 30px;
    border-radius: 5px;
    margin: 0px 100px 50px 0px;
}
.Fabric-components {
    margin: 20px 400px 20px 0px;
}
.Designer-quicklinks {
    font-size: 16px;
    float: right;
    padding: 0 30px 50px 0;
}
.Designer-steps {
    font-size: 18px;
    font-weight: 600;
    padding: 0px 0px 20px 0px;
}
.contact-quicklinks {
    font-size: 16px;
    display: flex;
    float: right;
    font-weight: 600;
    padding: 0 30px 50px 0;
    color: $cseo-brand-primary;

}
.Developer-steps {
    font-size: 18px;
    font-weight: 600;
    padding: 30px 0px 0px 0px;
}
.OverviewIntro {
    font-size: 18px;
    font-weight: 500;
}
.med-spacing {
   margin:50px 0px 0px 0px;
}
.lg-spacing {
    margin:100px 0px 0px 0px;
 }
.sm-spacing {
    margin:25px 0px 0px 0px;
 }
.no-spacing {
    margin:0;
 }
 .CodeHighlights {
    display: inline;
    background:#fff;
    font-family: monospace;
 }
 ol li.figma-options {
    padding: 0 0 20px 0px;
 }
 .image-margin {
     margin: 0 200px 0 0;
 }
 .image-width {
    width: 100%;
}
.image-width-75 {
    width: 75%;
}
.image-width-65 {
    width: 65%;
}
.image-width-50 {
    width: 50%;
}
.image-width-90 {
    width: 90%;
}
.image-width-95 {
    width: 95%;
}
.Image-spacing{
    padding: 20px 0 0 0;    
}
.Microsoft-logo {
    background:#f2f2f2;
    padding: 20px 0 10px 30px;
    z-index: 2;
    position: relative;
}
.resources-spacing {
    margin:30px 100px 50px 0;
 }
.resources-cols {
    display: inline-flex;
    flex-direction: column;
    position: relative;
    width: 17.25rem;
    margin: .5rem;
}
.resources-cols li {
    list-style: none;
    padding: 0 0 20px 0px;
}
.resources-list {
    padding: 0;
}
.quicklinks {
    color: $cseo-brand-primary;
    font-size: 18px;
    font-weight: normal;
    padding: 10px 0 0 0;
}
.footer-links {
    display: inline;
    background: #f2f2f2;
    padding: 20px 67px 0px 50px;
    text-align: right;
    text-decoration: none;
   
   a, p {
       color: #616161;
       padding: 0px 12px 0px 12px;
       display: inline;
       font-size: 11px;
   }
   a:hover {
    color: #000;
    
   }
}
.grid-legend-colors {
    width: 1rem;
    height: 1rem;
    border: 1px solid #767676;
    &.columns{
        background-color: #BBD0D8;
    }
    &.gutters{
        background-color: #72B294;
    }
    &.margins{
        background-color: #62E3A6;
    }
}
.grid-legend-col {
    display: inline-block;
    padding: 0px 10px 2px 0px;
}
.grid-legend-table-style{
    line-height: normal;
    padding: 10px 0px 0px 0px;
}
/*Edge Styling*/
@supports (-ms-ime-align: auto) {
    /* Microsoft EdgeV13&14 CSS styles go here */
    .nav-link, .sub-link {
        padding: 15px 0 0 5px !important;
        list-style-type: none;
      }
 }
 
 @supports (-ms-accelerator: true) {
     /* Microsoft EdgeV13 CSS styles go here */
     .nav-link, .sub-link {
        padding: 15px 0 0 5px !important;
        list-style-type: none;
      }
 }
 
 @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
     /* IE10 & IE11 CSS styles go here */
     .nav-link, .sub-link {
        padding: 15px 0 0 5px !important;
        list-style-type: none;
      }
 }




